body {
  margin: 0;
  padding: 0;
  min-width: 700px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbarContainer {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  padding: 5px 15px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#00BCD4), color-stop(white), color-stop(white), to(green));
  background-image: -webkit-linear-gradient(#00BCD4, white, white, green);
  background-image: linear-gradient(#00BCD4, white, white, green); }
  .navbarContainer .navbar {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    flex: 1 1; }
    .navbarContainer .navbar a {
      margin: 0 15px; }
    .navbarContainer .navbar .profileField {
      display: flex;
      flex-direction: column;
      align-items: center; }
      .navbarContainer .navbar .profileField img {
        width: 100px;
        border-radius: 50%; }
      .navbarContainer .navbar .profileField span {
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none; }

.text {
  font-size: 23px; }

.skillBarContainer {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px; }
  .skillBarContainer span {
    font-size: 20px;
    font-weight: 600; }
  .skillBarContainer .bar {
    height: 20px; }

.skillsItens {
  display: flex;
  flex-direction: row; }
  .skillsItens .languages {
    flex: 1 1; }
  .skillsItens .frameworks {
    flex: 1 1;
    margin-left: 15px !important; }
  .skillsItens .languages .title, .skillsItens .frameworks .title {
    display: flex;
    justify-content: center;
    font-size: 28px;
    font-weight: 800; }

.main{
    padding: 25px 35px;
}
